<template>
  <div class="params">
    <div id="leixin">
      <!-- <h3 class="title_container">库存管理\库存出库</h3> -->
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="库存组织" size="mini" prop="company_id">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @change="companyChange" filterable @focus="companyListInvok">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.outstock_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="客户名称" prop="customer_id" size="mini">
            <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok" @change="getCustomer">
              <ul class="customer customer-ul4 customer-ul2">
                <li>客户编号</li>
                <li>客户名称</li>
                <li>联系人</li>
                <li>联系电话</li>
                <li>区域</li>
              </ul>
              <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
                <ul class="customer customer-ul4">
                  <li :title="item.customer_id">{{ item.customer_id }}</li>
                  <li :title="item.customer_name">{{ item.customer_name }}</li>
                  <li :title="item.contact_name">{{item.contact_name}}</li>
                  <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                  <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="出库类型" size="mini">
            <el-select v-model="params.outstock_type_id" placeholder="请选择">
              <el-option label="其他出库" :value="10003"></el-option>
              <el-option label="盘亏出库" :value="10004"></el-option>
              <el-option label="调拨出库" :value="10005"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门" size="mini" prop="department_id">
            <el-select clearable v-model="params.department_id" placeholder="请选择" filterable @change="departmentChange" @focus="DepartmentListInvok">
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="关联单据号" size="mini">
            <el-input v-model="params.order_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select clearable v-model="params.auditor_status" placeholder="请选择">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="打印状态" size="mini">
            <el-select clearable v-model="params.print_status" placeholder="请选择">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未打印" :value="1">未打印</el-option>
              <el-option label="已打印" :value="2">已打印</el-option>
              <!-- <el-option label="作废" :value="3">作废</el-option> -->
            </el-select>
          </el-form-item>
          <!-- 	是否显示打印次数为0的单据 -->
          <!-- <el-form-item size="mini">
            <el-checkbox v-model="params.print_status" false-label="0" true-label="2" name="type" style="margin:0px 22px 0 62px">仅显示打印次数为0的单据</el-checkbox>
          </el-form-item> -->
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button icon="iconfont icon-pass" type="primary" @click="audit" size="mini" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain icon="iconfont icon-fanshenhe" @click="cancellation('V2InventoryAuditorNoPass')" size="mini" v-if="authority.b_nopass==1">弃审</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="cancellation('otherStockAuditorInvalid')" size="mini" v-if="authority.b_invalid==1">作废</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" v-loading="loading1" style="width: 100%" @selection-change="handleSelectionChange" max-height="600">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="125">
            <template slot-scope="scope">
              <el-button @click="seeDetail(scope, scope.row)" type="text" size="small" v-if="authority.b_info==1">
                查看
              </el-button>
              <el-button @click="modify(scope, scope.row)" type="text" size="small" v-if="scope.row.auditor_status==1 && authority.b_edit==1 && Number(scope.row.outstock_type_id) === 10003">
                修改
              </el-button>
              <el-button disabled type="text" size="small" v-else>
                修改
              </el-button>
              <template v-if="scope.row.after_auditor_print_status == 1">
                <el-button @click="printSheet(scope.row)" v-if="authority.b_print==1 && scope.row.auditor_status==2 " type="text" size="small">
                  打印
                </el-button>
                <el-button disabled v-else type="text" size="small">
                  打印
                </el-button>
              </template>
              <template v-else>
                <el-button @click="printSheet(scope.row)" v-if="authority.b_print==1" type="text" size="small">
                  打印
                </el-button>
              </template>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="库存组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="outstock_sn" width="180" label="单据号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_name" width="120" label="客户名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="outstock_num" width="180" label="出库数量">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="outstock_price" width="180" label="出库金额">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="department_name" width="180" label="部门">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_name" width="180" label="业务员">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="outstock_type_name" width="180" label="出库类型">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_user_name" width="150" label="创建人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" width="100" label="审核状态">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_name" width="150" label="审核人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" width="180" label="业务日期">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" width="180" label="单据时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_time" width="180" label="审核时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="order_sn" width="180" label="关联单据号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="update_user_name" width="150" label="修改人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="update_time" width="150" label="修改时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="abstract" label="备注">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="print_times" label="打印次数">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" :page-sizes="per_page" @current-change="handleCurrentChange" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
      <div v-if="printingStatusGet">
        <lodopDelivery ref="lodopDelivery" :title="title" @Search="onSearch"></lodopDelivery>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getData,
  CompanySwitchClear,
  opthionList,
  repertory,
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
import lodopDelivery from "common/components/public/PublicPrint.vue"; //其它出库单
export default {
  name: "instorageOthenoutstorageSalesoutstorageIndex",
  data() {
    return {
      lock: 0,
      pages: 1,
      title: "",
      loading:false,
      params: {
        outstock_type_id: "",
        auditor_status: 0,
        print_status: 0,
      },
      showDepartment: true,
    };
  },
  components: {
    lodopDelivery,
  },
  computed: {
    ...mapGetters(["othenoutstorageIndexGet", "printingStatusGet"]),
  },
  methods: {
    ...mapActions([
      "salesoutstorageAddACT",
      "othenoutstorageIndexACT",
      "salesoutstorageSeeACT",
      "printingStatus",
    ]),

    departmentChange(val) {
      if (val != "") {
        this.params.department_name = this.departmentList.filter(
          (obj) => obj.department_id === val
        )[0].department_name;
      }
    },
    getCustomer(val) {
      if (val != "") {
        this.params.customer_name = this.customerList.filter(
          (obj) => obj.customer_id === val
        )[0].customer_name;
      }
    },
    printSheet(row) {
      this.printingStatus(true);
      this.$set(this, "title", "otherOutboundSinglesToPrint");
      this.$nextTick(() => {
        this.$refs.lodopDelivery.printPdf({
          outstock_id: row.outstock_id,
        });
      });
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.timeValue = [];
      this.othenoutstorageIndexACT();
      this.$set(this, "params", {
        outstock_type_id: "",
        auditor_status: 0,
        print_status: 0,
      });
      this.$set(this, "searchParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "v2OtherStockGetOutstockList", //接口名
        vm: this, //传递的this
      });
      this.onSearch();
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          (obj) => obj.company_id === val
        )[0].company_name;
      }
      CompanySwitchClear(this, this.params);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1,
      });
      this.othenoutstorageIndexACT({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize,
      });
      getData(this.searchParams);
    },
    audit() {
      let vm = this;
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning",
        });
        return false;
      }
      // for(let i=0;i<this.multipleSelection.length;i++){
      //   if(this.multipleSelection[i].outstock_sn.substring(0, 4)==)
      // }
      let mulArry = this.multipleSelection.map((element) => {
        return element.outstock_sn.substring(0, 4);
      });
      let typeSet = new Set(mulArry);
      if (typeSet.size > 1) {
        this.$message({
          message: "请选择相同的出库类型",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      var cardoctor = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
        cardoctor.push({
          bill_id: this.multipleSelection[i].outstock_id,
          bill_type:
            this.multipleSelection[i].outstock_type_id === 10003
              ? 10
              : this.multipleSelection[i].outstock_type_id === 10004
              ? 11
              : 12,
        });
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "outstock_id",
        eachData: {
          outstock_id: rebateArr,
        },
        examineApi: "otherinventoryAuidtorPass",
        list: {
          UrlApi: "v2OtherStockGetOutstockList",
          data: this.TableData,
          esData: this.searchParams.params,
        },
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 10,
          bill_id: rebateArr,
          operation_type: 4,
          type_info: JSON.stringify(cardoctor),
        })
        .then(() => {
          // 校验安全库存金额
          this.$axMethod
            .safetyStockCheck({
              bill_logogram: [...typeSet][0],
              order_type: 2,
              bill_id: rebateArr,
            })
            .then(() => {
              repertory(
                this,
                {
                  goods_info: rebateArr,
                  status: 2,
                  type: 4,
                },
                function () {
                  vm.$common.examineFun(funcan);
                }
              );
            });
        });
    },
    //作废
    cancellation(API) {
      if (this.multipleSelection.length < 1) {
        let txt = "";
        if (API == "otherStockAuditorInvalid") {
          txt = "作废";
        } else if (API == "V2InventoryAuditorNoPass") {
          txt = "弃审";
        }
        this.$message({
          message: `请选择需要${txt}的信息`,
          type: "warning",
        });
        return false;
      }
      var addr = [];
      var cardoctor = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
        cardoctor.push({
          bill_id: this.multipleSelection[i].outstock_id,
          bill_type:
            this.multipleSelection[i].outstock_type_id === 10003
              ? 10
              : this.multipleSelection[i].outstock_type_id === 10004
              ? 11
              : 12,
        });
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "outstock_id",
        eachData: {
          outstock_id: rebateArr,
        },
        // examineApi: "otherStockAuditorInvalid",
        examineApi: API,
        list: {
          UrlApi: "v2OtherStockGetOutstockList",
          data: this.TableData,
          esData: this.searchParams.params,
        },
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 10,
          bill_id: rebateArr,
          operation_type: API == "otherStockAuditorInvalid" ? 6 : 5,
          type_info: JSON.stringify(cardoctor),
        })
        .then(() => {
          this.$common.examineFun(funcan);
        });
    },
    //删除
    deletes() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要删除的信息",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      var cardoctor = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
        cardoctor.push({
          bill_id: this.multipleSelection[i].outstock_id,
          bill_type:
            this.multipleSelection[i].outstock_type_id === 10003
              ? 10
              : this.multipleSelection[i].outstock_type_id === 10004
              ? 11
              : 12,
        });
      }
      var rebateArr = addr.toString();
      var deledata = {
        deleID: "outstock_id",
        deleSelect: this.multipleSelection,
        deleApi: "otherStockDelOutstock",
        list: {
          UrlApi: "v2OtherStockGetOutstockList",
          data: this.TableData,
          esData: this.searchParams.params,
        },
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 10,
          bill_id: rebateArr,
          operation_type: 3,
          type_info: JSON.stringify(cardoctor),
        })
        .then(() => {
          this.$common.deletes(deledata, "删除", this);
        });
    },
    addF() {
      this.salesoutstorageAddACT({
        outstock_id: "",
        goPage: this.goPage,
        // params: this.params,
        pageSize: this.pageSize,
      });
      this.$router.push({
        path: "/inventory/othenoutstorage/othenoutstorageAdd",
      });
    },
    modify(scope, row) {
      if (row && row.is_execute == 1) {
        this.$message.error("wms系统正在操作此单据，暂不支持单据修改");
        return false;
      }
      this.$router.push({
        path: "/inventory/othenoutstorage/othenoutstorageAdd",
      });

      this.salesoutstorageAddACT({
        outstock_id: row.outstock_id,
        goPage: this.goPage,
        // params: this.params,
        pageSize: this.pageSize,
      });
    },
    seeDetail(scope, row) {
      this.$router.push({
        path: "/inventory/othenoutstorage/othenoutstorageSee",
        query: {
          outstock_id: row.outstock_id,
        },
      });
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val,
      });
      getData(this.searchParams);
    },
  },
  mounted() {
    this.$set(this, "params", {
      outstock_type_id: "",
      auditor_status: 0,
      print_status: 0,
    });
    if (this.othenoutstorageIndexGet && this.othenoutstorageIndexGet.params) {
      if (this.othenoutstorageIndexGet.params.business_start_time) {
        const {
          business_start_time,
          business_end_time,
        } = this.othenoutstorageIndexGet.params;
        this.timeValue = this.timeValue.concat([
          business_start_time,
          business_end_time,
        ]);
      }
      this.$set(this, "params", this.othenoutstorageIndexGet.params);
      this.$set(this.params, "auditor_status", 0);
      this.$set(this.params, "print_status", 0);
      opthionList(this, this.othenoutstorageIndexGet.params);
    }
    if (!!this.othenoutstorageIndexGet.goPage) {
      this.params.page = this.othenoutstorageIndexGet.goPage;
      this.params.per_page = this.othenoutstorageIndexGet.pageSize;
      // this.goPage = this.othenoutstorageIndexGet.goPage; // 放这边不生效
    }
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "v2OtherStockGetOutstockList", //接口名
      vm: this, //传递的this
    });
    getData(this.searchParams);
  },
  created() {
    // 因在 mounted中显示第几页不准确故放到 created 中
    if (!!this.othenoutstorageIndexGet.goPage) {
      this.goPage = this.othenoutstorageIndexGet.goPage;
    }
  },
};
</script>
